.Button {
  display: inline-block;
  margin: 20px 0 0 30px;
  padding: 5px 15px;
  border-radius: 5px;
  border: none;
  background-color: rgb(84, 129, 211);
  color: white;
  font-family: BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu,
    Cantarell, 'Helvetica Neue', sans-serif;
  font-size: 16px;

  &:hover,
  &:focus {
    background-color: tomato;
  }
}
